.main {
  flex: 1;
  display: flex;
  margin: 3px;
  min-height: 0;
  min-width: 0;
  overflow: auto;
}

.preview.main {
  margin: 0;
}

.main.row {
  flex-direction: row;
}

.main.row > :first-child {
  /* border-right: 1px solid rgb(216, 216, 216); */
}

.main.column {
  flex-direction: column;
}

.main.column > :first-child {
  /* border-bottom: 1px solid rgb(216, 216, 216); */
}

.areaContainer {
  display: flex;
  overflow: auto;
  height: inherit;
  /* min-height: 0; */
  /* min-width: 0; */
}

.refresh :global(.reflex-container),
.threeColorTemplate :global(.reflex-container) {
  gap: 6px;
}

.refresh :global(.reflex-splitter),
.threeColorTemplate :global(.reflex-splitter) {
  border-color: #f2f2f2 !important;
  background-color: #f2f2f2 !important;
  transition: 0.25s all ease-in-out !important;
}

.refresh :global(.reflex-splitter):hover,
.threeColorTemplate :global(.reflex-splitter):hover {
  border-color: #bbb !important;
}

.refresh :global(.vertical) > :global(.reflex-splitter),
.threeColorTemplate :global(.vertical) > :global(.reflex-splitter) {
  width: 18px !important;
}

.refresh :global(.horizontal) > :global(.reflex-splitter),
.threeColorTemplate :global(.horizontal) > :global(.reflex-splitter) {
  height: 18px !important;
}
